
.m-login {
  position: relative;
  height: 520px;
}
.m-login .card-box {
    padding: 20px;
    border: 1px solid #ccc;
    background-color: #F9FAFC;
    width: 280px;
    position: absolute;
    right: 10px;
    top: 50%;
    margin-top: -150px;
}
.m-login .card-box .title {
      margin: 0px auto 30px auto;
      text-align: left;
      color: #505458;
      font-size: 16px;
}



